.design-and-development-main-container {
  display: flex;
  flex-direction: row;
}

.design-and-development-main-container-row-1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding-top: 4%;
  padding-bottom: 4%;
  padding-left: 100px;
  gap: 50px;
  background-color: #e5e5e5;
}

.design-and-development-main-container-row-1-card-1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 25px;
  width: 80%;
  padding: 3vh 2vw;
  gap: 0vh;
  align-self: flex-start;
  position: relative;
  background-color: white;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.4);
  cursor: pointer;
  transition: all 0.5s ease-in;
  text-decoration: none;
  color: black;
}

.design-and-development-main-container-row-1-card-1-icon {
  width: 10%;
}

.design-and-development-main-container-row-1-card-1 h1 {
  text-align: center;
  font-size: 2vw;
}

.design-and-development-main-container-row-1-card-1 p {
  text-align: center;
  line-height: 2;
  font-size: 1vw;
  z-index: 5;
}

.web-arrow-1 {
  /* position: absolute;
  top: 20rem;
  /* top: 125%; 
  left: -5%; /* Adjust the value according to your preference 
  transform: translateY(-50%);
  width: 30%; */

  position: absolute;
  top: 20vh;
  /* top: 125%; */
  left: -5vw; /* Adjust the value according to your preference */
  transform: rotate(7.5deg);
  height: 27.5vw;
}

.web-arrow-1:hover {
  cursor: default;
}

.web-triangle-1 {
  position: absolute;
  top: 2.5vh;
  left: -5vw; /* Adjust the value according to your preference */
  transform: translateY(-50%);
  width: 3vw;
  cursor: default;
}

.web-triangle-1:hover {
  cursor: default;
}

