.designing-websites-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

/* .designing-websites-container p {
  text-align: left;
  font-size: 1rem;
}
 */
.designing-websites-container-1 {
  flex: 1;
  /* height: 100vh; */
  display: flex;
  align-items: flex-start;
  justify-content:center;
  flex-direction: column;
  text-align: left;
  padding-left: 50px;
  padding-top: 4vh;
  padding-bottom: 4vh;
  gap: 2.75vh;
}

.designing-websites-container-1 h1 {
  font-size: 2em;
}

.designing-websites-container-1 p {
  font-size: 1rem;
  line-height: 2;
}
.designing-websites-container-2 {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-image: url("./designing-websites-1.png");
  background-size: 100% 100%;
  background-position: center; */
}

@media (max-width: 767px) {
  .designing-websites-container {
    flex-direction: column;
    padding: 4vh 50px;
    align-items: center;
  }

  .designing-websites-container-1 h1 {
    text-align: center;
  }
  
  .designing-websites-container-1 p {
    text-align: center;
  }
}