.grow-your-business-today-main-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("./grow-your-business-today.jpg");
  background-size: 120% 120%;
  background-repeat: no-repeat;
  transition: background-position 0.2s ease;
  padding: 0 5vw;
}

.grow-your-business-today-main-container-1 {
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 7%;
}

.grow-your-business-today-main-container-1 h1 {
  font-family: "Poppins", Sans-serif;
  text-align: center;
  font-size: 3rem;
  color: #fff;
}

.grow-your-business-today-main-container-1 button {
  font-size: 1.5rem;
  background-color: #8e06c2;
  color: #fff;
  border-radius: 25px;
  padding: 2vh 3vw;
  font-weight: bolder;
  cursor: pointer;
  border: none;
}
