.pricing-main-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  padding: 0 50px;
  gap: 2.5%;
  flex-wrap: wrap;
  min-height: 90vh;
}

.pricing-main-container-1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  /* align-items: stretch; */
  /* justify-content: space-evenly; */
  padding: 2.5% 5%;
  border: 1px solid gray;
  border-radius: 10px;
  gap: 5%;

  /*  */
  /* min-height: 90vh; */

}

.pricing-main-container-1 h1 {
  text-align: left;
}

.pricing-1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2px;
}

.pricing-main-container-1 p {
  text-align: left;
}

.pointers-in-card-1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: stretch;
  width: 100%;
  gap: 10px;
}

.pointers-in-card-1-check {
  background-color: grey;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.card-1-button {
  width: fit-content;
  height: fit-content;
  font-size: 1rem;
  padding: 5%;
  background-color: white;
  color: #8e06c2;
  border: 1px solid #8e06c2;
  margin-top: auto;
  cursor: pointer;
}

.pricing-main-container-1:hover .pointers-in-card-1-check {
  background-color: white;
}

.pricing-main-container-1:hover{
  cursor: pointer;
}

