.services-decription-container {
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 20%;
  background-color: #d9d9d9;
}

.services-decription-container h1 {
  text-align: center;
}

.services-decription-container p {
  text-align: center;
  line-height: 2;
}

.services-decription-container button {
  background-color: #693897;
  font-size: 1.25rem;
  color: #fff;
  width: fit-content;
  height: fit-content;
  border-radius: 10px;
  padding: 1% 1%;
  margin-top: 1%;
  transition: all 0.25s;
  border: none;
}

.services-decription-container button:hover {
  cursor: pointer;
  scale: 1.2;
  box-shadow: 0 0 10px 5px #e9d9d9;
}
